*,*::before,*::after{
  margin:0;
  padding:0;
  box-sizing:border-box;
  vertical-align:baseline
}

body{
  margin:0;
  --tw-bg-opacity:1;
  background-color:rgba(249, 250, 251, var(--tw-bg-opacity));
  font-family:"Roboto",sans-serif;
  -webkit-font-smoothing:antialiased;
  -moz-osx-font-smoothing:grayscale
}

code{
  font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace
}
.app-container{
  text-align:center
}
.input-box{
  height:2.5rem;
  font-size:0.75rem;
  line-height:1rem;
  padding-top:0.5rem;
  padding-left:0.5rem;
  width:100%;
  border-radius:.3rem
}

.input-box:focus{
  background-color:#e8f0fe;
  outline:1px solid #bbb
}

.trans-effect{
  transition:all 100ms ease-out;
  -webkit-transform-origin:0% -50%;
  transform-origin:0% -50%;
  -webkit-transform:scale(0.8);
  transform:scale(0.8)
}

.trans-effect-none{
  transition:all 100ms linear;
  -webkit-transform-origin:0% -50%;
  transform-origin:0% -50%;
  -webkit-transform:scale(1);
  transform:scale(1)
}
.phone-inner{
  top:6.25rem;
  left:9.375rem
}
.navbar-container{
  width:100%;
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content:center;
  justify-content:center;
  position:-webkit-sticky;
  position:sticky;
  top:0
}

.navbar-logo{
  display:-webkit-flex;
  display:flex;
  -webkit-align-items:center;
  align-items:center;
  -webkit-justify-content:center;
  justify-content:center
}

.navbar-links{
  width:14rem;
  display:-webkit-flex;
  display:flex;
  -webkit-align-items:center;
  align-items:center;
  -webkit-justify-content:flex-start;
  justify-content:flex-start
}

.menu-item:not(:first-child){
  margin-left:.75rem
}

.menu-item{
  cursor:pointer
}

.menu-item-gray{
  cursor:default
}
.cog-container{
  width:calc(13rem - 5px);
  height:5rem;
  position:absolute;
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction:column;
  flex-direction:column;
  -webkit-align-items:flex-start;
  align-items:flex-start;
  -webkit-justify-content:center;
  justify-content:center;
  border-radius:.2rem;
  background:#fff;
  box-shadow:-1px 1px 3px rgba(0,0,0,.1)
}

.cog-container li{
  padding-left:1rem
}
.file-input{
  opacity:0;
  width:.1px;
  height:.1px;
  position:absolute
}
.upload-modal-container{
  width:calc(100% - 2rem);
  left:1rem;
  height:calc(90% - 5rem);
  position:fixed;
  z-index:10;
  top:6.5rem;
  border-radius:.5rem;
  box-shadow:1px 1px 3px rgba(0,0,0,.2),-1px -1px 3px rgba(0,0,0,.2)
}

.file-preview{
  max-height:80%;
  width:100%;
  overflow-y:hidden;
  left:2.5%
}

.file-preview img{
  max-width:calc(80% - 2rem);
  max-height:80%
}

@media screen and (min-width: 1024px){
  .upload-modal-container{
    width:80%;
    left:10%
  }

  .file-preview{
    left:2.5%;
    height:calc(100% - 10rem)
  }

  .file-preview img{
    max-width:calc(80% - 1rem);
    max-height:80%
  }
}

@media screen and (min-width: 1440px){
  .upload-modal-container{
    width:50%;
    left:25%
  }

  .file-preview{
    left:2.5%;
    height:calc(100% - 10rem)
  }

  .file-preview img{
    max-width:calc(70% - 1rem);
    max-height:70%
  }
}
.photos-container{
  width:clamp(350px, 80%, 100%);
  box-shadow:0px 1px 3px rgba(0,0,0,.2);
  display:grid;
  grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));
  min-height:-webkit-min-content;
  min-height:min-content;
  grid-gap:1rem;
  gap:1rem;
  padding:1rem
}

.photo{
  height:auto;
  width:300px;
  border-radius:5px;
  overflow:hidden;
  display:-webkit-flex;
  display:flex;
  justify-self:center;
  -webkit-align-self:center;
  align-self:center;
  box-shadow:1px 1px 2px rgba(0,0,0,.2),-1px -1px 2px rgba(0,0,0,.2)
}

.photo img{
  width:100%;
  height:auto
}

@media screen and (min-width: 768px){
  .photos-container{
    grid-template-columns:repeat(auto-fit, minmax(300px, 1fr))
